const Card = ({name, email, id}) => {
	return (
		<div className='bg-light-green dib br3 pa3 ma2 grow shadow bw2 shadow-5'>
		<img src={`https://robohash.org/${id}?size=240x240`} alt='robots'/>
		    <div>
			    <h2>{name}</h2>
			    <p> {email}</p>
		    </div>
	    </div>
	);

}

export default Card;